<template>
  <div class="branch">
    <branch-options @search="search" ref="branchOptions"/>
    <branch-table :branchTotalSize="totalSize" :loading="loading" @search="search" :data="data" :pageShow="pageShow" ref="branchTable"/>
  </div>
</template>

<script>
import BranchOptions from './controls/branchOptions'
import BranchTable from './controls/branchTable'
import queryString from "querystring"
let urls = ['/physicalChild','/chinaMedChild','/dentistryChild'];
export default {
  name: 'branch',
  data() {
    return {
      data:[], //table数据
      totalSize: 0, //一共多少条数据
      pageShow: false,  //页码切换是否显示
      loading: false
    }
  },
  components: {
    BranchOptions,
    BranchTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      if( !this.$route.query ) return;
      this.loading = true;
      let { code } = this.$route.query;
      const optionParams = this.$refs.branchOptions.getData();
      const tableParams = this.$refs.branchTable.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      let url = urls[code];
      this.api.get(`${url}?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.totalSize = res.data.totalPage;
        this.pageShow = this.data.length > 20;
        this.dataInit();
      }).catch((err) => {
        console.log(err)
        this.$message(err.message)
      })
    },
    dataInit(){ //数据初始化
      this.data.map((item) => {
        item.area = item.area || item.province + item.city + item.country
      })
    }
  }
}
</script>
 
